
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  height: 60px;
  text-align: right;
  background-color: #fff;
  box-sizing: border-box;
  border-bottom: 1px solid #e6e6e6;
}
header > ul {
    padding: 10px 0;
}
header > ul > li {
      display: inline-block;
      height: 25px;
      line-height: 25px;
      padding: 0 20px;
}
header > ul > li:not(:last-child) {
        border-right: 1px solid #e6e6e6;
}
header > ul > li img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 10px;
        vertical-align: middle;
}
header > ul .name {
      color: #1F2D3D;
}



