
.drop-down-wrap {
  position: relative;
  display: inline-block;
  height: 21px;
  padding-bottom: 15px;
  cursor: pointer;
}
.drop-down-wrap .drop-down-text i {
    margin-left: 8px;
}
.drop-down-wrap .drop-down-action {
    position: absolute;
    right: -5px;
    margin-top: 8px;
    background-color: #fff;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    display: none;
}
.drop-down-wrap .drop-down-action ul {
      padding: 6px 0;
}
.drop-down-wrap .drop-down-action ul li {
        box-sizing: border-box;
        height: 30px;
        line-height: 1em;
        padding: 10px 25px;
        white-space: nowrap;
}
.drop-down-wrap .drop-down-action ul li:hover {
          background-color: rgba(26, 145, 242, 0.1);
}
.drop-down-wrap .caret-down {
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px;
    border-bottom-width: 0;
    border-color: #BFBFBF transparent transparent transparent;
    margin-bottom: 3px;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}
.drop-down-wrap:hover .drop-down-action {
    display: block;
}
.drop-down-wrap:hover .caret-down {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}



